.popup__saving {
  display: none;
  margin: 18px auto 0;
  padding: 0;
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  background: #000000;
  border: none;
  border-radius: 2px;
  color: #fff;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
}

@media screen and (max-width: 550px) {
  .popup__submit {
    margin-top: 15px;
    height: 46px;
    font-size: 14px;
  }
}
